*{
  font-family: 'Akshar', sans-serif;
font-family: 'My Soul', cursive;
font-family: 'Poppins', sans-serif;
font-family: 'Roboto', sans-serif;
}

/* this css for single card */
#card {
  margin-top: 5%;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 10px;
}
#card1 {
  padding: 15px;
  margin: 0px;
}
#id4 {
  position: absolute;
  top: 0%;
  left: 95.2%;
}

/* this css code for navbar */
.nav {
  background-color: aquamarine;
  color: black;
  width: 100%;
  height: 65px;
  padding-left: 10%;
}

.content {
  padding-top: 1%;
  padding-left: 1%;
}

/* input field and search  css*/
.box2 {
  text-align: center;
  margin-top: 10%;
}
input[type="text"] {
  height: 50px;
  width: 340px;
  padding: 0 25px;
  margin-right: 2px;
  border-radius: 25px 0 0 25px;
  outline: none;
}
#id3 {
  border-radius: 0px 25px 25px 0px;
  height: 50px;
  width: 150px;
  background: #ffc107;
  outline: none;
  border: none;
}

/* css for not found */
#content-2 {
  margin-top: 10%;
  text-align: center;
}
